import axios from 'axios'
import { Base64 } from 'js-base64'

const api = {
  getUrl() {
    var index = window.parent.location.pathname.lastIndexOf("/");
    var urlbase = window.parent.location.pathname.substring(0, index);
    if (urlbase != "/workarea") {
      return `${window.parent.location.origin}` + urlbase;
    }
    else {
      return `${window.parent.location.origin}`;
    }
  },
  getConfig() {
    return new Promise((resolve, reject) => {
      axios.get(this.getUrl() + '/filemanager/getconfig')
        .then(res => resolve(res))
        .catch(error => reject(error))
    })
  },
  changeDir(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/changedir', {
        to: params.to,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  getDir(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/getdir', {
        dir: params.dir,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  copyItems(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/copyitems', {
        destination: params.destination,
        items: params.items,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  moveItems(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/moveitems', {
        destination: params.destination,
        items: params.items,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  renameItem(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/renameitem', {
        from: params.from,
        to: params.to,
        destination: params.destination,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  batchDownload(params) {
    return new Promise((resolve, reject) => {
      axios.post('batchdownload', {
        items: params.items,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  zipItems(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/zipitems', {
        name: params.name,
        items: params.items,
        destination: params.destination,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  unzipItem(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/unzipitem', {
        item: params.item,
        destination: params.destination,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  removeItems(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/deleteitems', {
        items: params.items,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  createNew(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/createnew', {
        type: params.type,
        name: params.name,
        destination: params.destination,
      })
        .then(res => resolve(res.data.data))
        .catch(error => reject(error))
    })
  },
  downloadItem(params) {
    return new Promise((resolve, reject) => {
      axios.get(this.getUrl() + '/filemanager/download&path=' + encodeURIComponent(Base64.encode(params.path)),
        {
          transformResponse: [data => data],
        })
        .then(res => resolve(res.data))
        .catch(error => reject(error))
    })
  },
  saveContent(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/savecontent', {
        name: params.name,
        content: params.content,
      })
        .then(res => resolve(res.data))
        .catch(error => reject(error))
    })
  },
  getPath(params) {
    return new Promise((resolve, reject) => {
      axios.post(this.getUrl() + '/filemanager/getpath',
        {
          item: params.item,
        })
        .then(res => resolve(res.data))
        .catch(error => reject(error))
    })
  },
}

export default api
