<template>
  <div class="modal-card">
    <header class="modal-card-head">
      <p class="modal-card-title">
        {{ lang('Unzip') }}
      </p>
      <b-loading :is-full-page="false" :active.sync="searching" />
    </header>
    <section class="modal-card-body">
      <p>
        {{ lang('Do you want to unzip the file?') }}
      </p>
      <br>
      <label for="UploadWithoutUnzip">
        <input type="radio" id="UploadWithoutUnzip" name="unzip" value="1" checked>
          {{ lang('No') }} &nbsp
      </label>
      <br>
      <label for="UnzipHere">
        <input type="radio" id="UnzipHere" name="unzip" value="2">
          {{ lang('Unzip here') }} &nbsp
      </label>
      <br>
      <label for="UnzipInSubfolder">
        <input type="radio" id="UnzipInSubfolder" name="unzip" value="3">
          {{ lang('Unzip in subfolder ...') }} &nbsp
      </label>
    </section>
    <footer class="modal-card-foot">
      <button class="button" type="button" @click="unzip()">
        {{ lang('Accept') }}
      </button>
      <button class="button" type="button" @click="$parent.close()">
        {{ lang('Cancel') }}
      </button>
    </footer>
  </div>
</template>

<script>

export default {
  name: 'UnzipModal',
  props: [ 'files', 'dropZone' ],
  data() {
    return {
      active: false,
      term: '',
      results: [],
    }
  },
  mounted() {
    this.active = true

  },
  beforeDestroy() {
    this.active = false
  },
  methods: {
    unzip(){

      var ele = document.getElementsByTagName('input');
                for(let i = 0; i < ele.length; i++) {
                  if(ele[i].type == 'radio') {
                    if(ele[i].checked){
                      if(ele[i].value == 1){
                        this.$parent.close()
                      }
                      if(ele[i].value == 2){
                        this.$emit('unzipHere', true)
                        this.$parent.close()
                      }
                      if(ele[i].value == 3){
                        this.$emit('unzipIn', true)
                        this.$parent.close()
                      }
                    }
                  }
                }
    },
    unzipHere(){
      this.$emit('unzipHere', true)
      this.$parent.close()
    },
    unzipIn(){
      this.$emit('unzipIn', true)
      this.$parent.close()
    },
  },
}
</script>

