<template>
  <div>
    <div v-if="visible && dropZone == false" class="progress-box">
      <div class="box">
        <div>
          <div class="is-flex is-justify-between">
            <div class="is-flex">
              <a @click="toggleWindow">
                <b-icon :icon="progressVisible ? 'angle-down' : 'angle-up'" />
              </a>
              <span v-if="activeUploads">
                {{ lang('Uploading files', resumable.getSize() > 0 ? Math.round(resumable.progress() * 100) : 100,
                formatBytes(resumable.getSize()))
                }}
              </span>
              <span v-if="activeUploads && paused">
                ({{ lang('Paused') }})
              </span>
              <span v-if="!activeUploads">
                {{ lang('Done') }}
              </span>
            </div>
            <div class="is-flex">
              <a v-if="activeUploads" @click="togglePause()">
                <b-icon :icon="paused ? 'play-circle' : 'pause-circle'" />
              </a>
              <a class="progress-icon" @click="closeWindow()">
                <b-icon icon="times" />
              </a>
            </div>
          </div>
          <hr>
        </div>
        <div v-if="progressVisible" class="progress-items">
          <div v-for="(file, index) in resumable.files.slice().reverse()" :key="index">
            <div>
              <div>{{ file.relativePath != '/' ? file.relativePath : '' }}/{{ file.fileName }}</div>
              <div class="is-flex is-justify-between">
                <progress :class="[file.file.uploadingError ? 'is-danger' : 'is-primary', 'progress is-large']"
                  :value="file.size > 0 ? file.progress() * 100 : 100" max="100" />
                <a v-if="!file.isUploading() && file.file.uploadingError" class="progress-icon" @click="file.retry()">
                  <b-icon icon="redo" type="is-danger" />
                </a>
                <a v-else class="progress-icon" @click="file.cancel()">
                  <b-icon :icon="file.isComplete() ? 'check' : 'times'" />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Resumable from 'resumablejs'
import api from '../../api/api'
import UnzipModal from './UnzipModal'
import axios from 'axios'
import _ from 'lodash'

export default {
  name: 'Upload',
  props: ['files', 'dropZone'],
  data() {
    return {
      resumable: {},
      visible: false,
      paused: false,
      progressVisible: false,
      progress: 0,
    }
  },
  computed: {
    activeUploads() {
      return this.resumable.files.length > 0 && this.resumable.progress() < 1
    },
  },
  watch: {
    'files'(files) {
      _.forEach(files, file => {
        this.resumable.addFile(file)
      })
    },
  },
  mounted() {
    this.resumable = new Resumable({
      target: api.getUrl() + '/filemanager/upload',
      headers: {
        'x-csrf-token': axios.defaults.headers.common['x-csrf-token']
      },
      withCredentials: true,
      simultaneousUploads: this.$store.state.config.upload_simultaneous,
      minFileSize: 0,
      chunkSize: this.$store.state.config.upload_chunk_size
    })

    if (!this.resumable.support) {
      this.$dialog.alert({
        type: 'is-danger',
        message: this.lang('Browser not supported.'),
      })
      return
    }

    this.resumable.assignDrop(document.getElementById('dropzone'))

    this.resumable.on('fileAdded', (file) => {
      this.visible = true
      this.progressVisible = true

      if (file.relativePath === undefined || file.relativePath === null || file.relativePath == file.fileName) file.relativePath = this.$store.state.cwd.location
      else file.relativePath = [this.$store.state.cwd.location, file.relativePath].join('/').replace('//', '/').replace(file.fileName, '').replace(/\/$/, '')

      if (!this.paused) {
        this.resumable.upload()
      }

    })

    this.resumable.on('fileSuccess', (file, event) => {
      file.file.uploadingError = false
      var fileExtension = file.fileName.split('.').pop()
      var fileName = file.relativePath != '/' ? file.relativePath + '/' + file.fileName : '/' + file.fileName

      // In case response error
      let responseMessage = event
      if(responseMessage == '"Error storing file"') {
        console.log(event)
        this.handleError(responseMessage)
        this.resumable.removeFile(file)
        if(!this.activeUploads){
          this.closeWindow()
        }
        return
      }

      if (fileExtension === 'zip') {
        this.$modal.open({
          parent: this,
          hasModalCard: true,
          component: UnzipModal,
          events: {
            unzipHere: unzipHere => {
              this.isLoading = true
              if (unzipHere == true) {
                api.unzipItem({
                  item: fileName,
                  destination: file.relativePath != '/' ? file.relativePath : '/',
                })
                  .then(() => {
                    this.$forceUpdate()
                    api.getDir({
                      to: '',
                    })
                      .then(ret => {
                        this.$store.commit('setCwd', {
                          content: ret.files,
                          location: ret.location,
                        })
                      })
                      .catch(error => this.handleError(error))
                  })
                  .catch(error => {
                    this.isLoading = false
                    this.handleError(error)
                  })
              }

            },
            unzipIn: unzipIn => {
              this.isLoading = true
              if (unzipIn == true) {
                var relativePath = file.relativePath
                if (relativePath.charAt(relativePath.length - 1) == '/') {
                  relativePath = relativePath.slice(0, -1)
                }
                //var fileCurrentDir = relativePath.split('/').pop();
                var filenameWithExt = file.fileName
                var indexOfExt = filenameWithExt.lastIndexOf('.')
                var filenameWithoutExt = filenameWithExt.substring(0, indexOfExt)
                this.$dialog.prompt({
                  title: this.lang('Unzip in subfolder'),
                  cancelText: this.lang('Cancel'),
                  confirmText: this.lang('Unzip'),
                  inputAttrs: {
                    value: filenameWithoutExt,
                    maxlength: 100,
                    required: false,
                  },
                  onConfirm: (value) => {
                    value = value.replaceAll('/', '')
                    this.isLoading = true
                    if (value == '') {
                      api.unzipItem({
                        item: fileName,
                        destination: file.relativePath != '/' ? file.relativePath : '/',
                      })
                        .then(() => {
                          this.$forceUpdate()
                          api.getDir({
                            to: '',
                          })
                            .then(ret => {
                              this.$store.commit('setCwd', {
                                content: ret.files,
                                location: ret.location,
                              })
                            })
                            .catch(error => this.handleError(error))
                        })
                        .catch(error => {
                          this.isLoading = false
                          this.handleError(error)
                        })
                    } else {
                      api.createNew({
                        type: 'dir',
                        name: value,
                        destination: this.$store.state.cwd.location,
                      })
                        // TODO: cors is triggering this too early?
                        .then(() => {
                          this.isLoading = false
                          this.$forceUpdate()
                          api.unzipItem({
                            item: fileName,
                            destination: file.relativePath != '/' ? file.relativePath + '/' + value : '/' + value,
                          })
                            .then(() => {
                              this.$forceUpdate()
                              api.getDir({
                                to: '',
                              })
                                .then(ret => {
                                  this.$store.commit('setCwd', {
                                    content: ret.files,
                                    location: ret.location,
                                  })
                                })
                                .catch(error => this.handleError(error))
                            })
                            .catch(error => {
                              this.isLoading = false
                              this.handleError(error)
                            })
                        })
                        .catch(error => {
                          this.isLoading = false
                          this.handleError(error)
                        })
                      this.checked = []

                    }

                  }
                })
              }

            },

          },
        })
        // this.unzip(fileName, file)
      }
      if (this.$unzipHere == false) {
        api.unzipItem({
          item: fileName,
          destination: file.relativePath != '/' ? file.relativePath : '/',
        })
          .then(() => {
            this.$forceUpdate()
            api.getDir({
              to: '',
            })
              .then(ret => {
                this.$store.commit('setCwd', {
                  content: ret.files,
                  location: ret.location,
                })
              })
              .catch(error => this.handleError(error))
          })
          .catch(error => {
            this.isLoading = false
            this.handleError(error)
          })
      }
      /*
      this.$dialog.confirm({
      message: this.lang('Do you want to unzip the file?'),
      type: 'is-danger',
      cancelText: this.lang('Upload without unzip'),
      confirmText: this.lang('Unzip'),
      onConfirm: () => {
        this.$dialog.confirm({
        message: this.lang('Where do you want to unzip?'),
        type: 'is-danger',
        cancelText: this.lang('Unzip here'),
        confirmText: this.lang('Unzip in ...'),
        onConfirm: () => {
          this.isLoading = true
          api.unzipItem({
            item: fileName,
            destination: file.relativePath != '/' ? file.relativePath : '/',
          })
            .then(() => {
              this.$forceUpdate()
              api.getDir({
                to: '',
              })
                .then(ret => {
                  this.$store.commit('setCwd', {
                    content: ret.files,
                    location: ret.location,
                  })
                })
              .catch(error => this.handleError(error))
          })
          .catch(error => {
            this.isLoading = false
            this.handleError(error)
          })
        this.checked = []
        }
      })
    }
  })
  }*/
      this.$forceUpdate()
      api.getDir({
        to: '',
      })
        .then(ret => {
          this.$store.commit('setCwd', {
            content: ret.files,
            location: ret.location,
          })
          if (!this.activeUploads) {
            this.closeWindow()
          }
        })
        .catch(error => this.handleError(error))
    })
    this.resumable.on('fileError', (file) => {
      file.file.uploadingError = true
    })
  },
  methods: {
    unzip(fileName, file) {
      if (this.unzipHere == false) {
        api.unzipItem({
          item: fileName,
          destination: file.relativePath != '/' ? file.relativePath : '/',
        })
          .then(() => {
            this.$forceUpdate()
            api.getDir({
              to: '',
            })
              .then(ret => {
                this.$store.commit('setCwd', {
                  content: ret.files,
                  location: ret.location,
                })
              })
              .catch(error => this.handleError(error))
          })
          .catch(error => {
            this.isLoading = false
            this.handleError(error)
          })
      }
    },
    closeWindow() {
      if (this.activeUploads) {
        this.$dialog.confirm({
          message: this.lang('Are you sure you want to stop all uploads?'),
          type: 'is-danger',
          cancelText: this.lang('Cancel'),
          confirmText: this.lang('Confirm'),
          onConfirm: () => {
            this.resumable.cancel()
            this.visible = false
          }
        })
      } else {
        this.visible = false
        this.resumable.cancel()
      }
    },
    toggleWindow() {
      this.progressVisible = !this.progressVisible
    },
    togglePause() {
      if (this.paused) {
        this.resumable.upload()
        this.paused = false
      } else {
        this.resumable.pause()
        this.paused = true
      }
    },
  },
}
</script>

<style scoped>
.progress-icon {
  margin-left: 15px;
}

.progress-box {
  position: fixed;
  width: 100%;
  bottom: -30px;
  left: 0;
  padding: 25px;
  max-height: 50%;
  z-index: 1;
}

.progress-items {
  overflow-y: scroll;
  margin-right: -100px;
  padding-right: 100px;
  max-height: 300px;
  /* fix this */
}
</style>
